@import "../variables.scss";

.keyboard {
    margin-top: 3rem;

    @media screen and (max-width: 1070px) {
        margin-top: $gap;
        margin-left: calc($gap * 2);
    }

    &_display {
        font-size: 1.5rem;
        height: 9rem;
        word-wrap: break-word;
        overflow-y: auto;

        @media screen and (max-width: 1070px) {
            height: 0;
            min-height: 2.5rem;
            margin-bottom: $gap;
        }
    }

    &_syllables {
        display: flex;
        justify-content: flex-start;
        gap: calc($padding * 3);
    
        & > div {
            font-weight: 700;
            font-size: 1.5rem;
            height: calc($phoneme-dimensions/1.5);
            width: calc($phoneme-dimensions/1.5);
            border: $phoneme-border solid black;
            border-radius: $border-radius;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;

            & > img {
                height: calc($phoneme-dimensions * 0.6);
            }

            &:nth-child(2) {
                padding-bottom: 0.2rem;
            }

            &:nth-child(3) {
                padding-bottom: 0.2rem;
            }

            &:nth-child(4) > img {
                margin-top: 0.5rem;
            }

            &:nth-child(5) > img {
                margin-right: 0.2rem;
            }

            &:hover {
                cursor: pointer;
                opacity: 0.5;
            }
            
        } 
    }

    &_control {
        display: flex;
        gap: calc($padding * 3);
        margin: calc($padding * 3) 0;

        & > div:hover {
            cursor: pointer;
            opacity: 0.5;
        }
    }
}